export default "import React, {useCallback, useEffect} from 'react';\nimport Gallery from 'react-photo-gallery';\nimport SelectedImage from './SelectedImage';\nimport {useDispatch, useSelector} from 'react-redux';\nimport {onGetGalleryPhotos} from '../../../../redux/actions/Gallery';\nimport InfoView from '@crema/core/InfoView';\nimport {makeStyles} from '@material-ui/core';\n\nconst CustomImage = (props) => {\n  const photos = useSelector(({gallery}) => gallery.photos);\n  const dispatch = useDispatch();\n  useEffect(() => {\n    dispatch(onGetGalleryPhotos());\n  }, [dispatch]);\n\n  const imageRenderer = useCallback(\n    ({index, left, top, key, photo}) => (\n      <SelectedImage\n        key={key}\n        margin={'2px'}\n        index={index}\n        photo={photo}\n        left={left}\n        top={top}\n      />\n    ),\n    [],\n  );\n\n  const useStyles = makeStyles((theme) => ({\n    galleryPhoto: {\n      position: 'relative',\n\n      '& img': {\n        borderRadius: theme.overrides.MuiCard.root.borderRadius,\n        objectFit: 'cover',\n        maxHeight: 260,\n      },\n    },\n  }));\n\n  const classes = useStyles(props);\n\n  return (\n    <div className={classes.galleryPhoto}>\n      <Gallery photos={photos} renderImage={imageRenderer} />\n      <InfoView />\n    </div>\n  );\n};\nexport default CustomImage;\n";