export default "import React, {useEffect, useState} from 'react';\nimport Gallery from 'react-photo-gallery';\nimport arrayMove from 'array-move';\nimport {SortableContainer, SortableElement} from 'react-sortable-hoc';\nimport {useDispatch, useSelector} from 'react-redux';\nimport {makeStyles} from '@material-ui/core';\n\nimport Photo from './Photo';\nimport {onGetGalleryPhotos} from '../../../../redux/actions/Gallery';\nimport InfoView from '@crema/core/InfoView';\n\n/* popout the browser and maximize to see more rows! -> */\nconst SortablePhoto = SortableElement((item) => <Photo {...item} />);\nconst SortableGallery = SortableContainer(({items}) => (\n  <Gallery\n    photos={items}\n    renderImage={(props) => <SortablePhoto {...props} />}\n  />\n));\n\nconst ReactSortableHoc = (props) => {\n  const dispatch = useDispatch();\n  const photos = useSelector(({gallery}) => gallery.photos);\n  const [items, setItems] = useState(photos);\n\n  useEffect(() => {\n    dispatch(onGetGalleryPhotos());\n    if (photos.length > 0) {\n      setItems(photos);\n    }\n  }, [dispatch, photos]);\n\n  const onSortEnd = ({oldIndex, newIndex}) => {\n    setItems(arrayMove(items, oldIndex, newIndex));\n  };\n\n  const useStyles = makeStyles((theme) => ({\n    galleryPhoto: {\n      position: 'relative',\n\n      '& img': {\n        borderRadius: theme.overrides.MuiCard.root.borderRadius,\n        objectFit: 'cover',\n        maxHeight: 260,\n      },\n    },\n  }));\n\n  const classes = useStyles(props);\n\n  return (\n    <div className={classes.galleryPhoto}>\n      <SortableGallery items={items} onSortEnd={onSortEnd} axis={'xy'} />\n      <InfoView />\n    </div>\n  );\n};\nexport default ReactSortableHoc;\n";