export default "import React, {useCallback, useEffect, useState} from 'react';\nimport Gallery from 'react-photo-gallery';\nimport Carousel, {Modal, ModalGateway} from 'react-images';\nimport {useDispatch, useSelector} from 'react-redux';\n\nimport {onGetGalleryPhotos} from '../../../../redux/actions/Gallery';\nimport InfoView from '@crema/core/InfoView';\nimport {makeStyles} from '@material-ui/core';\n\nconst ReactPhotoGallery = (props) => {\n  const [currentImage, setCurrentImage] = useState(0);\n  const [viewerIsOpen, setViewerIsOpen] = useState(false);\n  const photos = useSelector(({gallery}) => gallery.photos);\n\n  const dispatch = useDispatch();\n  useEffect(() => {\n    dispatch(onGetGalleryPhotos());\n  }, [dispatch]);\n\n  const openLightBox = useCallback((event, {photo, index}) => {\n    setCurrentImage(index);\n    setViewerIsOpen(true);\n  }, []);\n\n  const closeLightbox = () => {\n    setCurrentImage(0);\n    setViewerIsOpen(false);\n  };\n\n  const useStyles = makeStyles((theme) => ({\n    galleryPhoto: {\n      position: 'relative',\n\n      '& img': {\n        borderRadius: theme.overrides.MuiCard.root.borderRadius,\n        objectFit: 'cover',\n        maxHeight: 260,\n      },\n    },\n  }));\n\n  const classes = useStyles(props);\n\n  return (\n    <div className={classes.galleryPhoto}>\n      <Gallery photos={photos} onClick={openLightBox} />\n      <ModalGateway>\n        {viewerIsOpen ? (\n          <Modal onClose={closeLightbox}>\n            <Carousel\n              currentIndex={currentImage}\n              views={photos.map((x) => ({\n                ...x,\n                srcset: x.srcSet,\n                caption: x.title,\n              }))}\n            />\n          </Modal>\n        ) : null}\n      </ModalGateway>\n      <InfoView />\n    </div>\n  );\n};\nexport default ReactPhotoGallery;\n";